.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

html { box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.sr-only { position: absolute; width: 1px; height: 1px; margin: -1px; border: 0; padding: 0; white-space: nowrap; -webkit-clip-path: inset(100%); clip-path: inset(100%); clip: rect(0 0 0 0); overflow: hidden;}

* {
  margin: 0;
  padding: 0;
}

html {
  font-size: 61.5%;
}

body {
  font-family: 'Courier New', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #6A6579;
  color: white;
  font-size: 1.6rem;
}

button {
  cursor: pointer;
  background-color: inherit;
  outline: none;
  /* border: none; */
  border-radius: 5px;
  color: white;
  padding: 10px;
  font-family: inherit;
}

a {
  text-decoration: none;
  color: white;
}

img {
  width: 100%;
  /* object-fit: cover; */
  -webkit-align-self: center;
          align-self: center;
  display: block;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* fonts */



li {
  list-style: none;
}

p {
  margin: 3% 0;
}


form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}


header {
  border-bottom: 3px solid palegoldenrod;
  /* background: whitesmoke; */
  padding: 10px 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  /* min-height: 50px; */
  height: 5%;
}

h1 {
  font-family: 'Poiret One', cursive;
  font-size: 2.5rem;
  margin: 0;
  font-weight: 100;
  /* letter-spacing: 1.5px; */
  /* width: 300px; */
}

h1 a {
  width: auto;
}

.logo {
  display: -webkit-flex;
  display: flex;
    /* color: black; */
}

.logo img {
  width: 25px;
  margin-right: 3%;
}

nav ul {
  /* border: 1px solid palegoldenrod; */

  display: -webkit-flex;

  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  min-width: 103px;
  height: 100%;

}

nav li {
  height: auto;
}

nav button {
  min-width: 45px;
  min-height: 45px;
  color: #FF999E;
}


.timerInputBox {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin: 5% 0;
}

.timerInputBox button {
  margin: 15px auto;
}

.timerInputBox div {
  display: -webkit-flex;
  display: flex;
  width: 30%;
  -webkit-align-items: center;
          align-items: center;
}

.timerInput {
  text-align: right;
  width: 100%;
}

label {
  margin-left: 2px;
}


main {
  /* border: 2px solid olive; */
  
  -webkit-flex: 2 1;
  
          flex: 2 1;
  width: 100%;
  margin: 5px 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}


.App {
  max-width: 1200px;
  width: 90%;

  margin: 0 auto;
  padding: 2% 0;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  min-height: 100vh;
  position: relative;
}



.popUp {
  text-align: left;
  position: absolute;
  background: rgba(0, 0, 0, 0.8);
  width: 110%;
  left: -5%;
  top: 15%;
  max-height: 70%;
  overflow-y: scroll;
  scrollbar-width: thin;
}

.popUp::-webkit-scrollbar {
    width: 7px;
    background: rgba(0, 0, 0, 0.7);
}

.popUp::-webkit-scrollbar-thumb {
    background: rgba(119, 119, 119, 0.5);
}

footer { 
  border-top: 3px solid palegoldenrod;
  
  width: 100%;
  font-size: 1.3rem;
}

